
  .body {
    background-color: rgb(247, 247, 247);
    height: 100%;
    width: 100%;
    margin: 0;
    display: flex;
    align-items: center;
    overflow-x: hidden;
    flex-direction: column;
  }
  
  .maincontainer {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    height: 80%;
    margin-top: 0;
    padding-top: 0;
  }
  
  .logincontainer {
    background-color: white;
    align-self: flex-start;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 325px;
    padding-left: 12px;
    padding-right: 12px;
    height: auto;
    border: 1px solid rgb(219,219,219);
    border-radius: 1px;
    margin-top: 140px;
  }
  
  
  
  .bottomlogin {
    display: flex;
    flex-direction: column;
    align-self: center;
    align-items: center;
    margin-bottom: 15px;
  }
  
  .signupcontainer {
    background-color: white;
    align-self: center;
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
    border: 1px solid rgb(219,219,219);
    margin: 10px;
    padding: 8px 0;
    gap: 5px;
  }
  
  
  .signupcontainer a {
    text-decoration: none;
    font-weight: 600;
    color: #0095f6;
  }
  
  .footerdiv {
    margin-top: 100px;
  }
  
  
  
  .logo {
    padding-top: 25px;
    margin-top: 25px;
    padding-bottom: 5px;
    margin-bottom: 25px;
    display: flex;
    align-self: center;
    align-items: center;
  }
  
  .logincontainer input {
    background: rgba(250,250,250);
    border: 0;
    flex: 1 0 auto;
    margin: 0;
    outline: 0;
    overflow: hidden;
    padding: 9px 0 7px 8px;
    text-overflow: ellipsis;
    caret-color: black!important;
  }
  
  .login-failed {
    padding: 3px 0;
    margin: 2px 0;
  }
  
  .usernamediv, .passworddiv {
    box-sizing: border-box;
    display: flex;
    width: 82.5%;
    position: relative;
    border-radius: 3px;
    border: 1px solid rgba(190, 190, 190, .5);
    margin: 3.5px 0;
  }
  
  .logincontainer input:focus {
    border: 1px solid rgba(190, 190, 190, .8);
  }
  
  
  .buttoncontainer {
    padding: 5px;
    margin-top: 8px;
    width: 86.5%;
    box-sizing: border-box;
    display: flex;
    position: relative;
    justify-content: center;
  }
  
  
  .buttoncontainer input {
    width: 100%;
    color: white;
    border: 1px solid white;
    padding: 6px 9px;
    border-radius: 4px;
    font-weight: 600;
    text-align: center;
    cursor: pointer;
  }
  
  
  
  
  
  .alt {
    display: inline-flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    gap: 10px;
  }
  
  .alt p {
    font-size: 12px;
    font-weight: 700;
    color: rgba(97, 97, 97, 0.8);
  }
  
  #horizontalline1, #horizontalline2 {
    border-bottom: 1px solid rgb(0, 0, 0);
    width: 36%;
    opacity: 30%;
  }
  
  #facebook-login  {
    text-align: center;
    display: flex;
    align-items: center;
    vertical-align: baseline;
    align-self: center;
    font-size: 14px;
    font-weight: 500;
    color: #385185;
    text-decoration: none;
    
    padding: 10px;
    margin-top: 5px;
  }
  
  .bottomlogin img {
    padding-right: 5px;
  }
  
  #forgotpw {
    margin-top: 10px;
    text-decoration: none;
    font-size: 12px;
    color: black;
    margin-bottom: 3px;
    padding-bottom: 0;
    
  }
  
  .underlogin {
    display: flex;
    align-items: center;
    align-self: center;
    flex-direction: column;
  }
  
  .apps {
    display: flex;
    flex-direction: row;
    gap: 8px;
  }
  
  footer {
    margin-top: 20px;
    height: auto;
    width: 100%;
    display: flex;
    justify-content: center;
    padding: 30px;
  }
  
  .footerdiv {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    
  }
  
  .footerdiv ul {
    display: flex;
    flex-direction: row;
    list-style: none;
    list-style-type: none;
    gap: 15px;
    align-self: center;
    padding: 0;
  }
  
  .footerdiv a {
    text-decoration: none;
    font-size: 12px;
    color: #8e8e8e;
  }
  
  #topfooter {
    margin-bottom: 0;
  }
  
  .logininfo {
    text-align: center;
    padding: 10px;
    margin-top: 15px;
    border: 1px solid black;
    background-color: bisque;
  }