.commentdiv > img {
    border-radius: 20px;
    border: 1px solid transparent;
}


.commentdiv {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 5px;
    padding-top: 1px;
    padding-left: 5px;
    margin-left: 10px;
    width: 95%;
}

.commentsection {
    display: flex;
    flex-direction: column;
    padding-bottom: 8px;
    margin-bottom: 20px;
}

.posterusernamediv {
    margin-left: 5px;
}

.posterusername {
    color: #537fa4;
    font-weight: bold;
    font-size: 14px;
    margin-top: 17px;
    cursor: pointer;
}

.captiondiv {
    margin-left: 3px;
}

.caption {
    font-size: 14px;
    margin-top: 17px;
}


.comments {
    border-top: 1px solid rgba(117, 117, 117,.25);
    display: flex;
    flex-direction: row;
}

.addcomment {
    width: 85%;;
}

.addcomment > input {
    background: #e0e1e2;
    border: 0;
    flex: 1 0 auto;
    margin: 0;
    outline: 0;
    overflow: hidden;
    padding: 9px 0 10px 8px;
    margin-left: 3px;
    text-overflow: ellipsis;
    width: 80%;
    caret-color: black;
}

.submitdiv > input {
    background: #e0e1e2;
    border: 0;
    flex: 1 0 auto;
    margin: 0;
    outline: 0;
    padding: 9px 0 7px 8px;
    font-weight: 600;

}

.newComment {
    display: flex;
}


.submitdiv {
    margin-left: 10px;
    padding-left: 10px;
    caret-color: none;
}

.c {
    margin-left: 5px;
    color: #537fa4;
    font-weight: bold;
    font-size: 14px;
    margin-top: 17px;
    cursor: default;
}

.comment {
    margin-left: 3px;
    font-size: 14px;
}

.delete {
    width: fit-content;
    height: fit-content;
    outline: none;
    border: none;
    background-color: transparent;
    cursor: pointer;
    margin-left: auto;
    color: red;
    opacity: 66%;
    align-self: center;
    display: none;
}

.delete svg {
    display: flex;
    align-self: center;
}


.commentdiv:hover > button {
    display: flex;
}

.comment {
    padding-top: 2px;
}