.likesbar {
    border-top: 1px solid rgba(117, 117, 117,.25);
    border-bottom: 1px solid rgba(117, 117, 117,.25);
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
    background-color: #e0e1e2;
    height: 45px;
}

.imgcontainer {
    background-color: transparent;
    height: 100%;
    display: inline-block;
    position: relative;
}

.template {
    margin: 20px;
    display:flex;
    align-self: center;
    flex-direction: column;
    box-shadow: 0px 8px 32px 8px rgba(0,0,0,0.35);
}

.emptyheart {
    opacity: 0;
}

.likedheart4 {
    opacity: 0.075;
    transition: opacity 0.05s ease-in;
}

.likedheart3 {
    opacity: 0.15;
    transition: opacity 0.05s ease-in;
}

.likedheart2 {
    opacity: 0.2;
    transition: opacity 0.075s ease-in;
}

.likedheart1 {
    opacity: 0.3;
    transition: opacity 0.2s linear;
}

.red {
    position: absolute;
    z-index: 50;
    top: 200px;
    left: 185px;
    color: red;
}

.grey {
    position: absolute;
    z-index: 50;
    top: 200px;
    left: 185px;
    color: rgb(94, 94, 94);
}

.feed_image {
    max-width: 600px;
    height: 600px;
    display: block;
    z-index: 0;
}

#heartsicon {
    cursor: pointer;
    padding: 5.5px;
    margin-left: 3px;
    margin-right: 3px;
}


.seperator {
    box-shadow: 0px 1px 7px 0px rgba(0,0,0,0.2);
    border-left: 1px solid rgba(117, 117, 117,.25);
    height: 100%;
    top: 10px;
}

.likes {
    width: 82.5%;
}

.likes p {
    margin-left: 10px;
}

.threedots {
    margin-left: 2px;
    padding-left: 10px;
    filter: invert(78%) sepia(7%) saturate(0%) hue-rotate(151deg) brightness(96%) contrast(82%);
}

.posterinfo {
    display: flex;
    flex-direction: row;
    padding: 10px;
}

.posterpfp > img {
    border-radius: 20px;
    margin-top: 10px;
    margin-left: 5px;
    margin-right: 5px;
    border: 0.5px solid rgba(0, 0, 0,.1);
}

.posterusernamediv {
    margin-left: 5px;
}

.posterusername {
    color: #537fa4;
    font-weight: bold;
    font-size: 14px;
    margin-top: 17px;
    cursor: pointer;
}

.captiondiv {
    margin-left: 3.85px;
}

.caption {
    font-size: 14px;
    margin-top: 17px;
}

