.main {
  margin: 0px;
  display: flex;
  flex-direction: column;
}

.feed {
  min-width: 33%;
  width: fit-content;
  height: fit-content;
  display: flex;
  flex-direction: column;
  align-items: center;
  align-self: center;
  background-color:#e0e1e2;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  gap: 155px;
  padding: 50px 0;
  caret-color: transparent;
}

body {
  background-color: rgb(247, 247, 247);
}