 
  .navbar {
    height:  50px;
    width: 100%;
    background: linear-gradient(#457ca5, #265F8b 70%);
    border: 1px solid #1E5F8D;
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    align-content: center;
    overflow: hidden;
    caret-color: transparent!important;
  }
  
  .home {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #09548d;
    border-left: 1px solid rgb(38,84,124);
    border-right: 1px solid rgb(38,84,124);
  }
  
  .home img {
    filter: invert(99%) sepia(1%) saturate(892%) hue-rotate(182deg) brightness(96%) contrast(102%);
  }
  
  #ig-logo {
    padding-top: 2px;
    filter: invert(99%);
    cursor: pointer;
  }
  
  .user {
    display: flex;
    border-left: 1px solid rgb(38,84,124);
    border-right: 1px solid rgb(38,84,124);
    align-items: center;
    gap: 10px;
    padding-left: 15px;
    padding-right: 15px;
    justify-content: center;
    cursor: pointer;
  }

  .user2 {
    display: flex;
    align-items: center;
    gap: 10px;
    padding-right: 5px;
    justify-content: center;
    cursor: pointer;
  }
  
  .user2 > span {
    color: white;
    font-weight: 500;
  }
  
  .user2 > img {
    border-radius: 2px;
    border: 0.5px solid rgba(0, 0, 0,.1);
  }

  .logout {
    display: flex;
    flex-direction: row;
    border-left: 1px solid rgb(38,84,124);
    width: 50%;
    color: white;
    height: 100%;
  }

  .logout > svg {
    padding-left: 10px;
    margin-left: 10px;
    display: flex;
    flex-direction: row;
    align-self: center;
    justify-self: center;
  }
  